import { Link } from "gatsby"
import React from "react"
import styled from "styled-components"

const HeaderContainer = styled.div`
  margin: 2rem 0;
  border-bottom: 0.2rem solid whitesmoke;
`

const HeaderTitle = styled(Link)`
  text-decoration: none;
  font-size: 3rem;
  color: inherit;
`

const HeaderSubtitle = styled.div`
  font-size: 1.2rem;
`

const NavigationContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
`

const NavigationItem = styled(Link)`
  text-decoration: none;
  color: inherit;
  padding: 0.3rem;
  margin: 0 0.3rem;
  transition: background 0.1s;

  :hover {
    background: whitesmoke;
  }
`

const Header = ({ siteTitle }) => (
  <HeaderContainer>
    <HeaderTitle to="/">{siteTitle}</HeaderTitle>
    <HeaderSubtitle>
      Software Developer at Mosaic Manufacturing.
      <br />
      Working on an online slicer for 3D printers.
    </HeaderSubtitle>
    <NavigationContainer>
      <NavigationItem to="/">Projects</NavigationItem>
      {/* <NavigationItem to="/notes">Notes</NavigationItem> */}
      <NavigationItem to="/contact">Contact</NavigationItem>
    </NavigationContainer>
  </HeaderContainer>
)

export default Header
